import React, { PureComponent } from 'react';
import { func, object } from 'prop-types';
import { connect } from 'react-redux';
import cloneDeep from 'lodash.clonedeep';
import Popup from 'reactjs-popup';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import {
  actionGetPokemonList,
  actionCatchPokemon
} from './../../redux/actions';
import List from '../../components/list';
import Sidebar from '../../components/sidebar';
import PageDetail from './components/PageDetail';
import Form from '../../components/form';

export class Landing extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      initialUrl: 'https://pokeapi.co/api/v2/pokemon',
      isShowSidebar: false,
      isShowPopup: false,
      detailId: '',
      pokemonCaught: {},
      configuration: {
        toast: {
          position: toast.POSITION.BOTTOM_CENTER,
          autoClose: false
        }
      }
    };

    this.handleListClick = this.handleListClick.bind(this);
    this.handlePokemon = this.handlePokemon.bind(this);
    this.handleCatch = this.handleCatch.bind(this);
    this.handleSaveNickname = this.handleSaveNickname.bind(this);
    this.handleClosePopup = this.handleClosePopup.bind(this);
    this.handleMore = this.handleMore.bind(this);
  }

  componentDidMount() {
    this.handlePokemon();
  }

  /**
   * jika data belum ada, maka akan hit api untuk mendapatkan list pokemon
   *
   * @memberof Landing
   */
  handlePokemon() {
    if (this.props.listPokemon.data.length === 0) {
      this.props.actionGetPokemonList(this.state.initialUrl);
    }
  }

  /**
   * func untuk handle saat user klik salah satu dari list pokemon
   *
   * @param {object} data (list dari masing masing komponen)
   * @memberof Landing
   */
  handleListClick(id) {
    this.setState({
      isShowSidebar: true,
      detailId: id
    });
  }

  /**
   * fungsi untuk menghandle ketika user menekan tombol "catch"
   *
   * @param {object} { status, data }
   * @memberof Landing
   */
  handleCatch({ status, data }) {
    const { configuration } = this.state;

    if (status === 'success') {
      this.setState({
        isShowPopup: true,
        pokemonCaught: data
      });
    } else {
      toast.error('Upss sorry, please try again', configuration.toast);
    }
  }

  /**
   * funtion untuk save pokemon
   *
   * @param {*} nickname
   * @memberof Landing
   */
  handleSaveNickname(nickname) {
    const { configuration, pokemonCaught } = this.state;
    let myPokemon = cloneDeep(pokemonCaught);

    myPokemon.nickname = nickname;

    this.setState(
      {
        isShowSidebar: false,
        detailId: ''
      },
      () => {
        this.props.actionCatchPokemon(myPokemon);
        this.handleClosePopup();
        toast.info('Yuhuuuu, you got it', {
          ...configuration.toast,
          autoClose: 2000
        });
      }
    );
  }

  /**
   * lakukan reset pokemon yang tertangkap saat close popup
   *
   * @memberof Landing
   */
  handleClosePopup() {
    this.setState({ isShowPopup: false, pokemonCaught: {} });
  }

  handleMore() {
    const { listPokemon } = this.props;

    this.props.actionGetPokemonList(listPokemon.next);
  }

  render() {
    const { listPokemon } = this.props;
    const { isShowSidebar, detailId, isShowPopup } = this.state;
    const { data = {} } = listPokemon;

    return (
      <div className="landing">
        <div className="title" style={{ marginBottom: '20px' }}>
          Pokemon List
        </div>
        <div className="row">
          {Object.keys(data).map(list => {
            const value = data[list];

            return (
              <div className="col-xs-12" key={list}>
                <List
                  id={value.id}
                  name={value.name}
                  onClick={this.handleListClick}
                />
              </div>
            );
          })}
        </div>

        <Sidebar isShow={isShowSidebar}>
          {detailId ? (
            <PageDetail
              id={detailId}
              onClickBack={() => {
                this.setState({
                  isShowSidebar: false,
                  detailId: ''
                });
              }}
              onCatch={this.handleCatch}
            />
          ) : null}
        </Sidebar>

        <Popup open={isShowPopup} modal closeOnDocumentClick>
          <Form
            onClickClose={this.handleClosePopup}
            onClickSave={this.handleSaveNickname}
          />
        </Popup>

        <h3 onClick={this.handleMore} style={{ margin: '10px 0' }}>
          More
        </h3>

        <ToastContainer />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  listPokemon: state.pokemon.list || {}
});
const mapDispatchToProps = { actionGetPokemonList, actionCatchPokemon };

Landing.propTypes = {
  actionGetPokemonList: func,
  actionCatchPokemon: func,
  listPokemon: object,
  history: object
};

export default connect(mapStateToProps, mapDispatchToProps)(Landing);
