import React from 'react';
import { array, string } from 'prop-types';
import get from 'lodash.get';

import helpers from '../../core/helpers';
import './style.scss';

function PokeTypes({ title, data, fillTo }) {
  return (
    <div className="poketypes-wrapper left">
      <div className="row">
        <h2>{title}</h2>
        {data.map((list, key) => {
          return (
            <div className="col-xs-6 poketypes" key={key}>
              <div
                className="poketypes__box"
                style={{
                  backgroundColor: helpers.convertStringToColor(
                    get(list, `${fillTo}.name`, '')
                  )
                }}
              >
                {helpers.titleCase(get(list, `${fillTo}.name`, ''))}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

PokeTypes.propTypes = {
  title: string,
  data: array,
  fillTo: string
};

PokeTypes.defaultProps = {
  data: [],
  fillTo: 'type'
};

export default PokeTypes;
