import React, { PureComponent } from 'react';
import { object, func } from 'prop-types';

import Box from '../box';
import ButtonBack from '../buttonBack';
import PokeTypes from '../pokeTypes';

import './style.scss';

export class Detail extends PureComponent {
  constructor(props) {
    super(props);

    this.handleCatch = this.handleCatch.bind(this);
  }

  /**
   * funntion yang menghandle ketika user menekan tombol catch
   * persentase dapatnya sebuah pokemon adalah 50%
   * data yang di kembalikan beruba statys = success atau fail
   * dan data pokemon
   *
   * @memberof Detail
   */
  handleCatch() {
    const random = Math.floor(Math.random() * Math.floor(2));

    this.props.onCatch({
      status: random === 1 ? 'success' : 'fail',
      data: this.props.data
    });
  }

  render() {
    const { onClickBack, data } = this.props;

    return (
      <div className="detail">
        <div className="detail__back">
          <ButtonBack onClick={onClickBack} />
        </div>
        <Box data={data} />
        <div className="detail__content">
          <div className="row" style={{ marginBottom: '30px' }}>
            <div className="col-xs-6">
              <h2>Height</h2>
              <h3>{data.height}</h3>
            </div>
            <div className="col-xs-6">
              <h2>Weight</h2>
              <h3>{data.weight}</h3>
            </div>
          </div>

          <div className="row" style={{ marginBottom: '30px' }}>
            <div className="col-xs-12">
              <PokeTypes
                title="Abilities"
                data={data.abilities}
                fillTo="ability"
              />
            </div>
          </div>

          <div className="row" style={{ marginBottom: '20px' }}>
            <div className="col-xs-12">
              <PokeTypes title="Types" data={data.types} />
            </div>
          </div>
        </div>

        <div className="detail__button vertical-middle">
          <button onClick={this.handleCatch} className="content">
            CATCH
          </button>
        </div>
      </div>
    );
  }
}

Detail.propTypes = {
  onClickBack: func,
  onCatch: func,
  data: object
};

export default Detail;
