import React, { useState } from 'react';
import { func } from 'prop-types';

import Button from './../button';
import './style.scss';

function Form({ onClickClose, onClickSave }) {
  const [nickname, setNickname] = useState('');

  function handleSubmit(e) {
    e.preventDefault();

    if (nickname) {
      onClickSave(nickname);
    }
  }

  return (
    <form className="form" onSubmit={handleSubmit}>
      <input
        type="text"
        value={nickname}
        onChange={e => {
          setNickname(e.target.value);
        }}
        placeholder="Pokemon Name"
      />

      <div className="row">
        <div className="col-xs-6">
          <Button label="Close" onClick={onClickClose} />
        </div>
        <div className="col-xs-6">
          <Button label="Save" type="success" onClick={handleSubmit} />
        </div>
      </div>
    </form>
  );
}

Form.propTypes = {
  onClickClose: func,
  onClickSave: func
};

export default Form;
