import React, { PureComponent } from 'react';
import { object, func } from 'prop-types';
import { connect } from 'react-redux';
import get from 'lodash.get';
import Popup from 'reactjs-popup';

import List from '../../components/list';
import ButtonBack from '../../components/buttonBack';
import Button from '../../components/button';
import helpers from '../../core/helpers';
import { actionReleasePokemon } from '../../redux/actions';

import './style.scss';

export class MyPokemon extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      isShowPopup: false,
      selectPokemon: ''
    };

    this.handleCancel = this.handleCancel.bind(this);
    this.handleRelease = this.handleRelease.bind(this);
  }

  /**
   * close popup
   *
   * @memberof MyPokemon
   */
  handleCancel() {
    this.setState({
      isShowPopup: false,
      selectPokemon: ''
    });
  }

  /**
   * func untuk melepaskan pokemon yang sudah di tangkap
   *
   * @memberof MyPokemon
   */
  handleRelease() {
    const { selectPokemon } = this.state;

    this.props.actionReleasePokemon(selectPokemon);
    this.handleCancel();
  }

  render() {
    const { myPokemon, history } = this.props;
    const { isShowPopup, selectPokemon } = this.state;

    return (
      <div className="mypokemon">
        <ButtonBack onClick={() => history.push('/')} />
        <div className="title" style={{ marginBottom: '20px' }}>
          My Pokemon
        </div>

        <div className="row">
          {Object.keys(myPokemon).map(list => {
            const value = myPokemon[list];
            const image = get(value, 'sprites.front_default', '');

            return (
              <div className="col-xs-12" key={list}>
                <List
                  id={value.nickname}
                  name={value.nickname}
                  image={image}
                  onClick={() => {
                    this.setState({
                      isShowPopup: true,
                      selectPokemon: value.nickname
                    });
                  }}
                />
              </div>
            );
          })}
        </div>

        <Popup open={isShowPopup} modal closeOnDocumentClick>
          <div>
            <p>Do you want to release {helpers.titleCase(selectPokemon)}?</p>
            <div className="row">
              <div className="col-xs-6">
                <Button label="No" onClick={this.handleCancel} />
              </div>
              <div className="col-xs-6">
                <Button
                  label="Yes"
                  type="danger"
                  onClick={this.handleRelease}
                />
              </div>
            </div>
          </div>
        </Popup>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  myPokemon: state.pokemon.myPokemon || {}
});
const mapDispatchToProps = { actionReleasePokemon };

MyPokemon.propTypes = {
  myPokemon: object,
  history: object,
  actionReleasePokemon: func
};

export default connect(mapStateToProps, mapDispatchToProps)(MyPokemon);
