import React, { PureComponent } from 'react';
import { object, func, shape, bool, string } from 'prop-types';
import { connect } from 'react-redux';
import get from 'lodash.get';

import { actionGetPokemonDetail } from '../../../../redux/actions';
import Detail from '../../../../components/detail';

import './style.scss';

export class PageDetail extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      data: {}
    };

    this.getDetail = this.getDetail.bind(this);
    this.handleDetail = this.handleDetail.bind(this);
  }

  componentDidMount() {
    this.getDetail();
  }

  componentDidUpdate(prevProps) {
    this.handleDetail(
      prevProps.detailPokemon.isLoading,
      this.props.detailPokemon.isLoading
    );
  }

  /**
   * mendapatkan detail dari masing masing pokemon
   * melalui value pada list, atau hit api untuk detail pokemon
   *
   * @memberof Detail
   */
  getDetail() {
    const { detailPokemon, id, actionGetPokemonDetail } = this.props;
    const detail = get(detailPokemon, `data[${id}]`, {});

    if (Object.keys(detail).length !== 0) {
      this.setState({
        data: detail
      });
    } else {
      actionGetPokemonDetail(`https://pokeapi.co/api/v2/pokemon/${id}`);
    }
  }

  /**
   * cek apakah melakukan hit untuk api detail pada pokemon
   *
   * @param {*} prevLoading (loading sebelum)
   * @param {*} curLoading (loading saat ini)
   * @memberof Detail
   */
  handleDetail(prevLoading, curLoading) {
    if (prevLoading !== curLoading && !curLoading) {
      const { detailPokemon, id } = this.props;
      const detail = get(detailPokemon, `data[${id}]`, {});

      this.setState({
        data: detail
      });
    }
  }

  render() {
    const { onClickBack, onCatch } = this.props;
    const { data } = this.state;

    return <Detail data={data} onClickBack={onClickBack} onCatch={onCatch} />;
  }
}

const mapStateToProps = state => ({
  detailPokemon: state.pokemon.detail || {}
});
const mapDispatchToProps = { actionGetPokemonDetail };

PageDetail.propTypes = {
  actionGetPokemonDetail: func,
  detailPokemon: shape({
    isLoading: bool,
    data: object
  }),
  id: string,
  onClickBack: func,
  onCatch: func
};

export default connect(mapStateToProps, mapDispatchToProps)(PageDetail);
