import { getPokemonList, getPokemonDetail } from './../../../models/pokemon';
import * as types from './types';

/**
 * untuk mendapatkan list pokemon
 *
 * @param {string} url
 */
export const actionGetPokemonList = url => ({
  types: [types.LOAD_LIST, types.LOAD_LIST_SUCCESS, types.LOAD_LIST_FAIL],
  promise: () => getPokemonList(url).then(res => normalizeData(res))
});

/**
 * untuk mendapatkan detail pada pokemon
 * url sudah termasuk id pokemon
 *
 * @param {string} url
 */
export const actionGetPokemonDetail = url => ({
  types: [types.LOAD_DETAIL, types.LOAD_DETAIL_SUCCESS, types.LOAD_DETAIL_FAIL],
  promise: () => getPokemonDetail(url).then(res => res)
});

/**
 * untuk menyimpan pokemon ke dalam list my pokemon
 *
 * @param {object} data (detail dari pokemon)
 */
export const actionCatchPokemon = data => ({
  type: types.CATCH_POKEMON,
  data
});

/**
 * untuk melepaskan komponen dari list my komponen
 *
 * @param {string} nickname
 */
export const actionReleasePokemon = nickname => ({
  type: types.RELEASE_POKEMON,
  nickname
});

/**
 * untuk set id pada list pokemon
 *
 * @param {object} data
 * @returns
 */
function normalizeData(data) {
  data.obj = {};
  data.results.forEach(list => {
    const arrUrl = list.url.split('/pokemon/');
    const id = arrUrl[1].replace('/', '');

    list.id = id;
    data.obj[id] = list;
  });

  delete data.results;

  return data;
}
