import * as types from './types';

const initialState = {
  list: {
    isLoading: false,
    next: '',
    data: []
  },
  detail: {
    isLoading: false,
    data: {}
  },
  myPokemon: {}
};

const ACTION_HANDLERS = {
  // LIST
  [types.LOAD_LIST]: state => {
    return {
      ...state,
      list: {
        ...state.list,
        isLoading: true
      }
    };
  },

  [types.LOAD_LIST_SUCCESS]: (state, action) => {
    const result = action.result || {};
    const data = { ...state.list.data, ...result.obj };
    const next = result.next;

    return {
      ...state,
      list: {
        ...state.list,
        isLoading: false,
        data,
        next
      }
    };
  },

  [types.LOAD_LIST_FAIL]: state => {
    return {
      ...state,
      list: {
        ...state.list,
        isLoading: false
      }
    };
  },

  // DETAIL
  [types.LOAD_DETAIL]: state => {
    return {
      ...state,
      detail: {
        ...state.detail,
        isLoading: true
      }
    };
  },

  [types.LOAD_DETAIL_SUCCESS]: (state, action) => {
    const result = action.result || {};
    let data = state.detail.data;

    data[result.id] = result;

    return {
      ...state,
      detail: {
        ...state.detail,
        isLoading: false,
        data
      }
    };
  },

  [types.LOAD_DETAIL_FAIL]: state => {
    return {
      ...state,
      detail: {
        ...state.detail,
        isLoading: false
      }
    };
  },

  // CATCH_POKEMON
  [types.CATCH_POKEMON]: (state, action) => {
    const data = action.data;
    let myPokemon = state.myPokemon;

    myPokemon[data.nickname] = data;

    return {
      ...state,
      myPokemon
    };
  },

  // RELEASE POKEMON
  [types.RELEASE_POKEMON]: (state, action) => {
    const nickname = action.nickname;
    let myPokemon = state.myPokemon;

    delete myPokemon[nickname];

    return {
      ...state,
      myPokemon
    };
  }
};

export default (state = initialState, action) => {
  const handler = ACTION_HANDLERS[action.type];

  return handler ? handler(state, action) : state;
};
